.projects {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 4rem;
  }
  
  @media only screen and (max-width: 915px) {
    .projects {
      grid-template-columns: 1fr 1fr;
    }
  }
  
  @media only screen and (max-width: 620px) {
    .projects {
      grid-template-columns: 1fr;
    }
  }
  
  .project__card {
    border-radius: 14px;
    border: 1px solid var(--paragraph);
    overflow: hidden;
    padding: 3rem;
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 2rem;
  }
  
.project__card  figure {
    position: relative;
  }
  
.project__card  figcaption {
    position: absolute;
    bottom: -0.5px;
    left: 50%;
    transform: translate(-50%, 0);
    background-color: var(--card);
    padding: 1rem 2rem;
    font-size: 1.4rem;
    width: 100.3%;
    border-bottom-left-radius: 14px;
    border-bottom-right-radius: 14px;
  }
  
  .project__card .project__image {
    height: 20rem;
    border-radius: 14px;
    overflow: hidden;
  }
  
  .project__card .project__image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
  
  .actions {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }
  
  .project__keywords {
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    align-content: start;
  }
  
  .project__keywords span {
    font-size: 1.1rem;
    padding: 5px;
    border-radius: 5px;
    border: 1px solid;
    /* background-color: var(--card); */
    color: var(--subheadline);
  }
  
  .project__card .actions a {
    text-decoration: none;
    color: var(--header);
    border: 1px solid var(--header);
    padding: 10px 20px;
    border-radius: 10px;
  }
  
  .card a:hover {
    box-shadow: var(--shadow-light);
  }
  