footer{
    border-top: 1px solid var(--paragraph);
}

footer ul {
  display: flex;
  justify-content: space-between;
  list-style: none;
  align-items: center;
  gap: 2.5rem;
}
footer ul::after,
footer ul::before {
  content: "";
  display: block;
  background-color: var(--paragraph);
  height: 1px;
  flex: 1;
}

.copyright-notice {
  text-align: center;
}
