.form {
    /* background-image: linear-gradient(
        105deg,
        rgba(255, 255, 255, 0.9) 0%,
        rgba(255, 255, 255, 0.9) 50%,
        rgba(0, 0, 0, 0) 50%
      ), */
    /* url(./tech.jpg); */
    color: var(--paragraph);
    background-image: linear-gradient(
        105deg,
        rgba(38, 39, 44, 0.9) 0%,
        rgba(38, 39, 44, 0.9) 50%,
        rgba(0, 0, 0, 0) 50%
      ),
      url(./tech.jpg);
    background-size: 100%;
    border-radius: 3px;
  }
  
  .contact__form {
    width: 50%;
    padding: 6rem;
  }
  
  input,
  textarea {
    display: block;
    width: 80%;
    border: none;
    background-color: transparent;
    border-bottom: 2px solid var(--paragraph);
    margin-bottom: 2rem;
    padding: 1rem 0;
    box-sizing: content-box;
    transition: border-color 0.5s ease-in-out; /* Modify as needed */
    transform-origin: left;
    color: var(--header);
    font-weight: normal;
    font-size: 1.5rem;
  }
  
  input:focus,
  textarea:focus {
    border: none;
    outline: none;
    border-bottom: 2px solid var(--primary);
    
  }
  
  .message {
    height: 100px;
  }
  
  .form h2 {
    /* color: var(--card) !important;
    text-align: center; */
    margin-bottom: 5rem;
  }
  
  .form textarea {
    resize: none;
  }
  